.formulario{
    p{
        font-size: 1.4rem;
        color: darken($grisOscuro, 10);
        margin: 2rem 0 0 0;
    }
    legend{
        font-size: 2rem;
        color: darken($grisOscuro, 10);
    }
    label{
        text-transform: uppercase;
        display: block;
    }

    input:not([type="submit"]),
    textarea,
    select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
        margin-bottom: 1rem;
    }
    textarea{
        height: 20rem;
    }
    .forma-contacto{
        max-width: 20rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
}