/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.W-100 {
  width: 100%;
}

.boton {
  background-color: #71B100;
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton:hover {
  background-color: #619800;
  cursor: pointer;
}
.boton a {
  color: #FFFFFF;
}

.boton-inline {
  background-color: #71B100;
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-inline:hover {
  background-color: #619800;
  cursor: pointer;
}
.boton-inline a {
  color: #FFFFFF;
}

.boton-rojo-block {
  background-color: red;
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #e60000;
  cursor: pointer;
}

.boton-verde-block {
  background-color: green;
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #006700;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: rgb(241, 197, 20);
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #deb50d;
  cursor: pointer;
}

.boton-transparente {
  background-color: rgba(20, 20, 20, 0);
  text-decoration: none;
  color: white;
  font-weight: 700;
  padding-top: 0;
  margin: 0;
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  border: none;
  border-radius: 0;
  padding: 2px;
}
.boton-transparente:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  cursor: pointer;
}
.boton-transparente:hover {
  border-bottom: 3px solid rgb(104, 192, 160);
}
.boton-transparente:focus-within {
  border-bottom: 3px solid rgb(104, 192, 160);
}

.boton-seleccion {
  align-items: center;
  background-color: rgba(36, 37, 45, 0.561);
  border: none;
  border-radius: 0;
  color: #fff;
  display: flex;
  font-weight: 700;
  height: 100%;
  margin: 0;
  padding: 2px;
  text-decoration: none;
  width: 100%;
  border-left: 5px solid rgb(104, 192, 160);
}
.boton-seleccion:hover {
  border-bottom: 3px solid rgb(104, 192, 160);
}

.boton-transparente-en {
  background-color: rgba(20, 20, 20, 0);
  text-decoration: none;
  color: white;
  font-weight: 700;
  padding-top: 0;
  margin: 0;
  display: block;
  align-items: center;
  height: 100%;
  width: 100%;
  border: none;
  border-radius: 0;
  padding: 0;
  width: 6.7rem;
}
.boton-transparente-en:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  cursor: pointer;
}
.boton-transparente-en span {
  opacity: 0;
}
.boton-transparente-en:hover {
  border-bottom: 3px solid rgb(104, 192, 160);
}
.boton-transparente-en:focus-within {
  border-bottom: 3px solid rgb(104, 192, 160);
}

.boton-transparente-menu {
  background-color: rgba(20, 20, 20, 0);
  text-decoration: none;
  color: white;
  font-weight: 700;
  padding-top: 0;
  margin: 0;
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  border: none;
  border-radius: 0;
  border-radius: 80%;
  width: 5rem;
  height: 5rem;
  padding: 2px;
  margin: 0 auto;
  padding: 2px;
  border-bottom: 3px solid rgb(104, 192, 160);
}
.boton-transparente-menu:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  cursor: pointer;
}

.boton-transparente-menu-en {
  display: block;
  padding: 2rem;
  height: 5rem;
  width: 7rem;
  border-radius: 8px;
  color: white;
}
.boton-transparente-menu-en:hover {
  background-color: black;
  color: white;
  cursor: pointer;
}
.boton-transparente-menu-en span {
  opacity: 0;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode td {
  color: #FFFFFF;
}
.dark-mode .anuncio p, .dark-mode .anuncio h3 {
  color: black;
}

.not-webp.inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 2rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyrig {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: end;
  }
}
.derecha .darkmode {
  width: 3rem;
  margin-top: 1rem;
  filter: invert(100%);
}
.derecha .darkmode:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
  .barra a img {
    width: 30rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.5rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobil {
    display: none;
  }
}
.mobil img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 768px) {
  .icono-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #e1e1e1;
}
.anuncio picture {
  justify-content: center;
}
.anuncio img {
  max-width: 50rem;
  margin: 0 auto;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio .precio {
  color: #71B100;
  font-size: 2.4rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 1;
  margin: 0 auto;
  height: 4rem;
  width: 4rem;
}
.iconos-caracteristicas li p {
  justify-content: center;
}

.imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.imagen-pocicion {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-pocicion h2 {
  font-size: 4rem;
  color: #FFFFFF;
  font-weight: 700;
}
.imagen-pocicion p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: black;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: green;
}
.texto-entrada span {
  color: #E08709;
}

.textimonial {
  background-color: #71B100;
  font-size: 1.5rem;
  padding: 5rem;
  border-radius: 2rem;
  color: #FFFFFF;
}
.textimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.textimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 6rem;
  display: block;
  position: absolute;
  left: -3rem;
}
.textimonial p {
  text-align: end;
}

.formulario p {
  font-size: 1.4rem;
  color: #1a1a1a;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #1a1a1a;
}
.formulario label {
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 20rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.seccion-nosotros {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}

.alerta {
  color: red;
  text-align: center;
  width: 100%;
  height: 1REM;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: rgb(69, 180, 69);
}
table.propiedades thead th {
  color: white;
  padding: 1rem;
}
table.propiedades .imagen {
  width: 8rem;
}

.imagen-update {
  width: 20rem;
}

* {
  margin: 0;
  padding: 0;
}

.publicidad img {
  width: 100%;
  height: 100%;
}

.loginPage {
  z-index: 9;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.7);
}
.loginPage .login {
  background-color: rgba(145, 196, 241, 0.336);
  backdrop-filter: blur(4px);
  border-radius: 2%;
  height: 70%;
  width: 30%;
  display: grid;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}
.loginPage .login .picture-logo {
  margin-top: -3rem;
}
.loginPage .login .picture-logo .logo {
  max-width: 8rem;
  height: 9rem;
  border-radius: 50%;
  margin: 0 auto;
}
.loginPage .login .formulario {
  height: 100%;
  width: 100%;
  margin-bottom: 0;
}
.loginPage .login .formulario input:not([type=submit]) {
  width: 100%;
  margin-bottom: 0;
  border: none;
  border-bottom: 2px solid white;
  background: transparent;
  outline: none;
  font-size: 12px;
  color: black;
}
.loginPage .login .formulario input:focus {
  border-bottom: 3px solid rgb(104, 192, 160);
  color: white;
}
.loginPage .login .formulario label {
  font-size: 13px;
  block-size: 900;
}
.loginPage .login .formulario p {
  text-align: left;
  margin-top: 0;
  font-size: 9px;
  margin-bottom: 1rem;
}
.loginPage .login .formulario p:hover {
  cursor: pointer;
  color: white;
  block-size: 700;
}
.loginPage .login .formulario .ojos {
  display: flex;
  position: relative;
  width: 100%;
  height: 1px;
}
.loginPage .login .formulario .ojos .ojocerrado {
  position: absolute;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  right: 0;
  top: 1.2rem;
}
.loginPage .login .formulario .ojos .ojocerrado:hover {
  color: rgb(104, 192, 160);
}

.over {
  z-index: 99;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.535);
}

.modalaccion {
  width: 40%;
  height: 50%;
  background-color: white;
  border-radius: 10px;
}

/* dimenciones de la imagen */
.over picture {
  display: block;
  width: 40rem;
  height: 40rem;
  border-radius: 10px;
}

/* boton de cerrar la ventana modal */
.exit {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: white;
  color: black;
  font-weight: 9000;
  display: grid;
  place-items: center;
}

/* cursor al momento de pocicionarse en el boton para cerrar modal */
.exit:hover {
  color: white;
  background-color: red;
  cursor: pointer;
}

:root {
  --color-menu:rgba(36, 111, 136, 0.797);
  --color-texto-heder: rgba(81, 83, 84, 0.926);
  --color-div-contenido: transparent;
  --color-div-footer: transparent;
  --body-color: white;
  --texto:rgb(46, 42, 42);
  --trajetas-div: transparent;
}

.dashboard {
  display: grid;
  grid-template-columns: 20% 80%;
  height: 100vh;
  width: 100%;
}
.dashboard .menu-dash {
  z-index: 99999;
  grid-column: 1/2;
  display: grid;
  grid-template-rows: 10% 10% 10% 10% 10% 10% 30%;
  width: 100%;
  height: 100vh;
  background-color: var(--color-menu);
  backdrop-filter: blur(4px);
  border-radius: 10px;
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0), 0 6px 20px 0 rgba(0, 0, 0, 0);
  overflow: hidden;
}
.dashboard .menu-dash .dash {
  grid-row: 2/3;
}
.dashboard .seccion-dashboard {
  margin-left: 10px;
  background-color: transparent;
  display: grid;
  grid-template-rows: 7% 86% 7%;
  overflow: hidden;
}
.dashboard .seccion-dashboard .header-dashboard {
  border-bottom: 1px solid rgba(70, 67, 67, 0.568);
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 3px;
  border-radius: 5px;
  grid-row: 1/2;
  background-color: rgba(255, 255, 255, 0);
}
.dashboard .seccion-dashboard .content-dashboard {
  height: 100%;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 3px;
  border-radius: 5px;
  grid-row: 2/3;
  background-color: var(--color-div-contenido);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0), 0 6px 20px 0 rgba(0, 0, 0, 0);
  overflow: auto;
}
.dashboard .seccion-dashboard .footer-dashboard {
  border-top: 1px solid rgba(70, 67, 67, 0.568);
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 3px;
  border-radius: 5px;
  grid-row: 3/4;
  background-color: var(--color-div-footer);
}
.dashboard .seccion-dashboard .footer-dashboard p {
  color: var(--texto);
}

